@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200&family=Montserrat:wght@200;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  margin: 10px;
  padding: 1rem;
}

body {
  font-family: 'Montserrat',
    sans-serif;
  background-color: rgb(0, 91, 151);
}

.container {
  /* width: 50%; */
  margin: 10px;
  display: grid;
  grid-gap: 2px;
  grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
  grid-template-rows: repeat(5, 1fr);
}


/*trying putting it in cards*/
.card {
  width: 180px;
  height: 350px;
  padding: 8px;
  display: block;
  position: relative;
  margin: 10px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.3;
  box-shadow: 0 10px 10px 3px rgb(145, 169, 219);
  background-color: rgb(255, 255, 255);
}

img {
  width: 100%;
  height: 150px;
}

a {
  text-decoration: none;
}

.card a {
  position: absolute;
  bottom: 5px;
  color: green;
  cursor: pointer;
}

h1 {
  color: white;
}
